.asteroidCardImage {
    width: 71px;
    height: 72px;
    position: absolute;
    top: 28px;
    left: 122px;
}

@media (max-width: 1100px) {
    .asteroidCardImage {
        width: 56px;
        height: 56px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
